<template>
  <v-app id="inspire">
    <v-card color="grey">
      <v-toolbar dark height="65px">
        <v-tooltip right>
          <template v-slot:activator="{ on }">
            <!-- <v-btn v-on="on">
              <v-icon>mdi-chevron-left</v-icon>
            </v-btn> -->

            <div absolute large icon v-on="on">
              <v-switch
                flat
                inset
                loading="dark"
                dense
                v-model="$vuetify.theme.dark"
                hide-details
                color="black"
                :src="require('./assets/dark.png')"
              />
            </div>
          </template>
          <span>White / Dark</span>
        </v-tooltip>

        <v-spacer></v-spacer>
        <v-col cols="7" md="3">
          <v-text-field
            color="red"
            clearable
            hide-details
            label="Search"
            solo-inverted
            v-model="vsearch"
            v-on:keyup.enter="listMovies()"
          ></v-text-field>
        </v-col>
        <v-spacer></v-spacer>

        <v-card class="px-1 v-indexMenu">
          <v-navigation-drawer
            dark
            mini-variant
            mini-variant-width="50"
            permanent
          >
            <v-list>
              <v-list-item>
                <v-tooltip v-model="show" left>
                  <template v-slot:activator="{ on }">
                    <v-btn v-on="on" @click.stop="mini = !mini">
                      <v-img
                        :src="require('./assets/info.png')"
                        aspect-ratio="1"
                        width="30"
                        @click.stop="about = true"
                      ></v-img>
                    </v-btn>
                  </template>
                  <span>About</span>
                </v-tooltip>
              </v-list-item>
              <v-tooltip v-model="show2" left>
                <template v-slot:activator="{ on }">
                  <v-btn
                    v-on="on"
                    width="50"
                    height="3px"
                    icon
                    color="white"
                    @click.stop="(mini = !mini), exit()"
                    >X</v-btn
                  >
                </template>
                <span>Exit</span>
              </v-tooltip>
            </v-list>
          </v-navigation-drawer>
        </v-card>
      </v-toolbar>

      <v-row justify="center">
        <v-dialog v-model="about" width="550px">
          <v-card class="mx-auto" dark max-width="550">
            infomovie was created to help find information about media content
            on the internet provided by IMDb® API.
          </v-card>
        </v-dialog>
      </v-row>

      <MovieList />

      <div v-if="this.$store.state.modinfo.movies != 0" class="text-center">
        <v-col cols="12" md="12">
          <div @click="listPage()">
            <v-pagination
              class="page"
              v-model="vpage"
              :length="leng"
              :total-visible="5"
              prev-icon="mdi-menu-left"
              next-icon="mdi-menu-right"
              color="black"
              @click="listPage()"
            >
            </v-pagination>
          </div>
        </v-col>
      </div>

      <v-col></v-col>
      <v-footer app height="28" color="black">
        <span style="color:white;">&copy; InfoMovie 2020.</span>
      </v-footer>
    </v-card>
  </v-app>
</template>

<script>
import MovieList from "./components/MovieList";
import { mapGetters } from "vuex";
export default {
  name: "App",
  components: {
    MovieList,
  },
  data() {
    return {
      show: false,
      show2: false,
      about: false,
      drawer: null,
      mini: true,
      vpage: 1,
      vsearch: null,
    };
  },

  computed: {
    ...mapGetters(["search, page"]),

    leng() {
      let leng = Math.round(this.$store.state.modinfo.result / 10);
      return leng;
    },

    search: {
      get() {
        return this.$store.state.search;
      },

      set(newSearch) {
        this.$store.commit("SET_SEARCH", newSearch);
      },
    },

    page: {
      get() {
        return this.$store.state.vpage;
      },

      set(newPage) {
        this.$store.commit("SET_PAGE", newPage);
      },
    },
  },
  methods: {
    exit() {
      window.close();
    },

    async listMovies() {
      this.search = this.vsearch;
      if (this.$store.state.modinfo.movies != null) {
        this.vpage = 1;
      }
      this.page = this.vpage;
      this.movies = this.$store.state.modinfo.movies;
      await this.$store.dispatch("listMovies");
    },

    async listPage() {
      this.page = this.vpage;
      this.movies = this.$store.state.modinfo.movies;
      await this.$store.dispatch("listMovies");
    },
  },
};
</script>
